@charset "UTF-8";

@mixin includeFontello() {

  @font-face {
    font-family: 'fontello';
    src: url('#{$fontelloFontPath}/fontello.eot?71914645');
    src: url('#{$fontelloFontPath}/fontello.eot?71914645#iefix') format('embedded-opentype'),
    url('#{$fontelloFontPath}/fontello.woff?71914645') format('woff'),
    url('#{$fontelloFontPath}/fontello.ttf?71914645') format('truetype'),
    url('#{$fontelloFontPath}/fontello.svg?71914645#fontello') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  /* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
  /* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
  /*
  @media screen and (-webkit-min-device-pixel-ratio:0) {
    @font-face {
      font-family: 'fontello';
      src: url('../font/fontello.svg?71914645#fontello') format('svg');
    }
  }
  */

  [class^="icon-"]:before, [class*=" icon-"]:before {
    font-family: "fontello";
    font-style: normal;
    font-weight: normal;
    speak: none;
    display: inline-block;
    text-decoration: inherit;
    margin-right: .01em;
    margin-left: $iconSpacing;
    text-align: center;

    /* For safety - reset parent styles, that can break glyph codes*/
    font-variant: normal;
    text-transform: none;

    /* fix buttons height, for twitter bootstrap */
    line-height: 1em;

    &.left-align {
      margin-left: 0;
    }
  }

  // icons in buttons:
  .button, .dropdown  {
    [class^="icon-"]:before, [class*=" icon-"]:before {
      margin-right:$iconSpacingButton;
      margin-left:$iconSpacingButton;
    }
  }

